const extensionMeta = document.createElement('meta')
extensionMeta.name = 'k6-browser-recorder'
extensionMeta.content = JSON.stringify({
  version: process.env.APP_VERSION,
  name: process.env.APP_NAME,
  platform: process.env.PLATFORM === 'browser' ? 'firefox' : process.env.PLATFORM,
})
document.head.appendChild(extensionMeta)
